<template>
  <div id="app">
    <h2>Memento</h2>
    <div class="motto">Easy to use VueJs countdown component</div>

    <div class="controls">
      <label>Format <input type="text" v-model="format"/></label> <label>Time <input type="text" v-model="time"/></label>
      <memento style="margin: 2rem auto; font-family: sans-serif; font-size: 1.6rem;" :t="time">
        {{format}}
      </memento>
    </div>
    <div>
      <pre><code>&lt;memento style="margin: 2rem auto; font-family: sans-serif; font-size: 1.6rem;" :t="time"&gt;
        {{format}}
&lt;/memento&gt;</code></pre>
    </div>
  </div>
</template>

<script>
import Memento from './components/Memento'

export default {
  name: 'App',
  components: {
    'memento' : Memento,
  },
  data() {
    return {
      format: "#h hours #m minutes #s seconds to BOOM",
      time: "2018-12-31 23:59:59 GMT+3"
    }
  }
}
</script>

<style>

#app {
  font-family: Arial, 'Avenir', Helvetica, Arial, sans-serif;
  font-size: 1rem;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  width: 800px;
  max-width: 100%;
  margin: 6rem auto;
}

h2 {
  font-family: Montserrat, sans-serif;
  font-size: 2rem;
  margin-bottom: 1rem;
}

.motto {
  margin-bottom: 1rem;
}

.controls {
  margin-top: 0.5rem;
}

.controls input {
  padding: 0 0.2rem;
  line-height: 1.6rem;
}

.controls label {
  height: 2rem;
  line-height: 2rem;
  display: inline-block;
  vertical-align: text-bottom;
}

code {
  padding: 0.5rem;
  font-family: monospace;
  border: 1px solid #333;
  text-align: left;
  white-space: pre;
  display: block;
  max-width: 100%;
  width: auto;
  margin: auto;
}

</style>



