<template>
  <div class="memento-countdown">{{result}}</div>
</template>

<script>
var dateDiff = require('date-diff')

var info = console.info
function getDate() { return new Date() }

export default {
  name: 'Memento',
  props: ['t'],
  data () {
    return {
      format: '#h : #m : #s',
      now : '',
    }
  },
  methods: {
    setFormat(format) {
      if (format) {
        this.format = format
      } else {
        // this.format = ''
      }
    },
    updateTimer() {
      this.now = new Date()
      // console.info(this.res)
    }
  },
  computed: {
    countdown () {
      return 'test'
    },
    then () {
      return new Date(this.t)
    },
    result () {
      let d = this.diff
      // console.info(d)
      let r = this.format
      r = r.replace(/#d/,d[0])
      r = r.replace(/#h/,d[1])
      r = r.replace(/#m/,d[2])
      r = r.replace(/#s/,d[3])
      return r
    },
    diff () {
      if (!this.then || !this.now) {
        return[]
      }

      let diff
      let s
      let m
      let h
      let d
      try {
        // console.info(this.then, this.now)
        diff = this.then.getTime() - this.now.getTime()
      } catch (e) {
        // console.info('bad date')
        return
      }

      diff = Math.floor(diff / 1000)
      if (diff < 0) {
        return [0,0,0,0];
      }

      d = Math.floor( diff / 60 / 60 / 24 )
      h = Math.floor( diff / 60 / 60 )
      m = Math.floor( diff / 60 )
      s = Math.floor( diff )

      if (d > 0) {
        h = h - d * 24;
        m = m - d * 24 * 60;
        s = s - d * 24 * 60 * 60;
      }
      if (h > 0) {
        m = m - h * 60;
        s = s - h * 60 * 60;
      }
      if (m > 0) {
        s = s - m * 60;
      }

      if (s >= 60) {
        s = s - 60;
        m++;
      }
      if (m >= 60) {
        m = 0;
        h++;
      }
      if (h >= 24) {
        h = 0;
        d++;
      }
      // s = Math.floor(diff / 1000)

      // d = Math.round(diff.days())
      // h = Math.round(diff.hours()) - d * 24
      // m = Math.round(diff.minutes()) - d * 24 * 60 - h * 60
      // s = Math.round(diff.seconds()) - d * 24 * 60 * 60 - h * 60 * 60 - m * 60

      // s = s < 60 ? 60 - s : s
      // let d = Math.floor( diff / ( 24 * 60 * 60 ) )
      // let h = Math.floor( d * 24 / ( 24 * 60 ) )
      // let m = Math.floor( diff / ( 24 * 60 ) ) - d * 60 - h * 24
      return [d,h,m,s];

    }
  },
  mounted() {
    // console.console.info('Memento mounted')
    let format = this.$slots.default[0].text;
    // console.info(this.$slots.default[0]);
    this.$slots.default[0].text = '';
    this.setFormat(format);
    this.now = new Date();
    this.updateTimer();
    setInterval( this.updateTimer, 1000 );
  }
}
</script>

<style scoped>
  .memento-countdown {

  }
</style>



